
.MovieListTitle {
  color: #446179;
  padding-top: 15px;
  font-size: 22px;
  font-family: "Zen Tokyo Zoo", cursive;
  text-align: center;
}
.MovieListImg {
  width: 100%;
  /* height: 220px; */
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.MovieListImg:hover {
  transform: scale(1.03);
  cursor: zoom-in;
}